import * as React from "react";
import Box from "@mui/material/Box";
import Header from "../../Components/Header.js";
import Modal from "../../Components/Modal.js";
import "./OrganisationalPages.css";
import { Button } from "@mui/material";
import { Link } from "react-router-dom";

const solutions = [
  {
    file: "../solutions/Orga-Qualitätsmanagement_Integrale Planung.pdf",
  },
  {
    file: "../solutions/Orga-Qualitätsmanagement_Qualitätssicherung bei der Ausführung.pdf",
  },
  {
    file: "../solutions/Orga-Qualitätsmanagement_Raumweise Einregulierung der Lüftung.pdf",
  },
];

export default function Qualitätsmanagement() {
  return (
    <div className="container">
      <Header />
      <Box className="pageBox">
        <div
          style={{
            backgroundImage:
              "url(../orga/qualitätsmanagement/qualitätsmanagement.png)",
          }}
          className="backgroundImg"
        ></div>
        <div style={{ fontSize: "120%" }}>
          <img
            src="../orga/qualitätsmanagement/qualitätsmanagement_icon.png"
            className="icon"
            alt="Qualitätsmanagement"
          />
          <div className="modalContainer">
            {solutions.length > 0 ? (
              solutions.map((props) => (
                <Modal
                  {...props}
                  key={props.file}
                  title={props.file
                    .split("/", 4)[2]
                    .split("_", 2)[1]
                    .split(".", 1)
                    .toString()}
                />
              ))
            ) : (
              <div>
                <h1>Ihre Ideen könnten genau hier stehen!</h1>
                <Link to="/idee-teilen" style={{ textDecoration: "none" }}>
                  <Button variant="outlined" sx={{ width: "30%" }}>
                    Idee Teilen
                  </Button>
                </Link>
              </div>
            )}
          </div>
          <div style={{ marginTop: "16%" }}>
            <img
              src="../orga/qualitätsmanagement/qualitätsmanagement_footer.png"
              className="footerImg"
              alt="Qualitätsmanagement"
            />
          </div>
        </div>
      </Box>
    </div>
  );
}
